import React, { Component } from "react"
import PropTypes from "prop-types"
import { Row, Col, Nav, Navbar } from 'react-bootstrap'

const linkStyle = {
  color : "rgba(0,0,0,0.7)"
}

class Header extends  Component {
  constructor({ siteTitle }){
    this.state = {
      location : "/"
    }
  }

  componentDidMount(){
    if(typeof window !== 'undefined' && window.location.protocol)
    {
      if(window.location.pathname === '/')
        this.setState({
          location : "/"
        })
      else if(window.location.pathname === '/about')
        this.setState({
          location : "/about"
        })
      else if(window.location.pathname === '/work')
        this.setState({
          location : "/work"
        })
      else if(window.location.pathname === '/project')
        this.setState({
          location : "/project"
        })
      else
        this.setState({
          location : "/blog"
        })

    }
  }

  render(){
    return(
      <header>
        <Row className="justify-content">
          <Col lg={{span: 10, offset:1}} sm="12" md="8" className="align-center">
            <Navbar sticky="top" collapseOnSelect expand="lg" variant="light">
              <Navbar.Brand href="/">Cengiz Cebeci</Navbar.Brand>
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Navbar.Collapse id="responsive-navbar-nav" className="justify-content-end">
                <Nav>
                  <Nav.Link style={{linkStyle}} active={this.location === '/'} href="/">Home</Nav.Link>
                  <Nav.Link style={{linkStyle}} active={this.location === '/about'} href="/about">About</Nav.Link>
                  <Nav.Link style={{linkStyle}} active={this.location === '/work'} href="/work">Work</Nav.Link>
                  <Nav.Link style={{linkStyle}} active={this.location === '/project'} href="/project">Project</Nav.Link>
                  <Nav.Link style={{linkStyle}} active={this.location === '/blog'} href="/blog">Blog</Nav.Link>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
          </Col>
        </Row>
      </header>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string.isRequired,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
